import {render} from 'react-dom';
import { Button, Row, Col, Table,Tabs,Modal} from 'antd';
import { EditOutlined,AppstoreOutlined, MailOutlined, SettingOutlined } from '@ant-design/icons';
import Top from '../../../component/top/index';
