import React, {Component} from 'react';
import {render} from 'react-dom';
import { Breadcrumb,Descriptions,Tag,Button,Row,Col,Card,List} from 'antd';
import { StarOutlined } from '@ant-design/icons';
import axios from 'axios';
import Top from '../../../../component/top/index';
import Title from '../../../../component/title/index';
// import Footer from "../../component/footer";
import './index.css';
import {request} from "../../../../utils/request";


class policyPreview extends Component {
    constructor(props){
        super(props);
        this.state = {
            id:props.match.params ? props.match.params.id : null,
            policy:{}
        };
        this.listData = [
            {
                title: '',
                time:'2016-02-26',
                link:'www.baidu.com'
            },
            {
                title: '2019',
                time:'2016-02-26',
                link:'www.baidu.com'
            },
            {
                title: '2019',
                time:'2016-02-26',
                link:'www.baidu.com'
            },
        ];
    }
    componentDidMount() {
        this.getDefalutData();
    }
    getDefalutData = async() =>{
        const {data} = await request(`/policy/get/${this.state.id}`, 'GET'); //
        this.setState({
            policy:data.policy,
            resource_file_list:data.resource_file_list
        })
    }
    render() {
        const {policy,resource_file_list=[]} = this.state;
        const labelStr = policy.label_add_str ? policy.label_add_str.split(",") : null;
        return (
            <div className="policyPreview-template">
                <Top />
                <div className="max-weight-box"><Title name="" /></div>
                <div className="policyPreview-label-box">
                    <div className="max-weight-box">
                       <div className="policyPreview-descriptions">
                        <Descriptions>
                            <Descriptions.Item label="" span={3}><span>{policy.title}</span></Descriptions.Item>
                            <Descriptions.Item label=""><span title=""></span></Descriptions.Item>
                            <Descriptions.Item label="">{policy.release_date}</Descriptions.Item>
                            <Descriptions.Item label="">{policy.post_shop_name}</Descriptions.Item>
                            {labelStr ? <Descriptions.Item label="" span={3} className="labelAdd">
                                {labelStr.map((item,idx)=><Tag key={idx}>{item}</Tag>)}</Descriptions.Item> : null}
                            {labelStr ? <Descriptions.Item label="">
                                {policy.life_date}
                            </Descriptions.Item> : null}
                        </Descriptions>
                       </div>
                        <div className="policyPreview-content-box">
                            <div className="policyPreview-content">
                                {/*<p className="policyPreview-content-title">2018()</p>*/}
                                <div className="policyPreview-content-text">
                                    <div dangerouslySetInnerHTML = {{ __html:policy.content }}></div>
                                    <Row>
                                        <Col span={2}></Col>
                                        <Col>
                                            {resource_file_list ?
                                                resource_file_list.map((item,idx)=><p><a href={item.image_url} key={idx}>{item.file_ori_name}</a></p>)
                                                : null}

                                        </Col>
                                    </Row>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    };
}

export default policyPreview;