import React, {Component} from 'react';
import {render} from 'react-dom';
import {Form, Input, Button, message} from 'antd';
import Top from './../../component/top';
import './index.css';
import cookie from "react-cookies";
import {request} from "../../utils/request";

const layout = {
    labelCol: {span: 8},
    wrapperCol: {span: 16},
};

const validateMessages = {
    required: '!',
    types: {
        email: 'Not a validate email!',
        number: 'Not a validate number!',
    },
    number: {
        range: 'Must be between ${min} and ${max}',
    },
};


class ForgotYour extends Component {
    constructor(props){
        super(props);
        this.state = {

        }
    }
    onFinish = async (values) => {
        //
        const responest = await request('/common/forget-password','POST',{...values});
        const data = responest.data;
        if(data && data.success){
            message.success(data.msg);
            cookie.save('userId', data.data.id);
            cookie.save('userName', values.username);
            cookie.save('userType', 1);
            setTimeout(()=>{
                this.props.history.push('/');
            },1000);
        }else{
            message.error(data.msg);
        }
        // axios.post('/company/register',{
        //     ...values
        // })
        //     .then(function(response) {
        //         console.log(response.data);
        //         console.log(response.status);
        //         console.log(response.statusText);
        //         console.log(response.headers);
        //         console.log(response.config);
        //     });

    };
    onReset = () => {
        this.props.form.resetFields();
    };
    render() {
        return (
            <div className="forgotYour-template">
                <Top />
                <div className="forgotYour-form-box">
                    <div className="forgotYour-title"></div>
                    <Form {...layout} name="nest-messages" onFinish={this.onFinish} validateMessages={validateMessages}>
                        <Form.Item name="mobile" label="  " rules={[{required: true}]}>
                            <Input placeholder=""/>
                        </Form.Item>
                        <Form.Item label="">
                            <Form.Item name="yzm" noStyle  rules={[{required: true}]}>
                                <Input placeholder="" min={1} max={10} style={{width:200,marginRight:10}} />
                            </Form.Item>
                            <Button className="ant-form-text"> </Button>
                        </Form.Item>
                        <Form.Item name='password' label="" rules={[
                            {
                                required: true,
                                message: ''
                            },
                            {
                                message: '6-25',
                                min:6,
                                max:25
                            },
                            ({ getFieldValue }) => ({
                                validator(rule, value) {
                                    if(/^(?=.*[a-zA-Z])(?=.*[0-9]).*$/.test(value)){
                                        return Promise.resolve()
                                    }
                                    else{
                                        return Promise.reject('6-25');
                                    }
                                },
                            }),
                        ]}>
                            <Input.Password placeholder="6-25"/>
                        </Form.Item>
                        <Form.Item
                            name="confirmPassword"
                            label=""
                            dependencies={['password']}
                            hasFeedback
                            rules={[
                                {
                                    required: true,
                                    message: '',
                                },
                                ({ getFieldValue }) => ({
                                    validator(rule, value) {
                                        if (!value || getFieldValue('password') === value) {
                                            return Promise.resolve();
                                        }

                                        return Promise.reject('!');
                                    },
                                }),
                            ]}>
                            <Input.Password placeholder=""/>
                        </Form.Item>
                        <Form.Item wrapperCol={{span: 19, offset: 5}} className="forgotYour-button">
                            <Button type="primary" htmlType="submit">
                                
                            </Button>
                            <p><a href="/login"></a></p>
                        </Form.Item>
                    </Form>
                </div>
                {/*<Footer/>*/}
            </div>
        );
    };
}

export default ForgotYour;