import React, {Component} from 'react';
import {render} from 'react-dom';
import { Table, Input, Row, Col, Button, Select, DatePicker, Breadcrumb } from 'antd';
import { ArrowUpOutlined,ArrowDownOutlined,PlusOutlined,MinusOutlined } from '@ant-design/icons';
import { Link } from "react-router-dom";
import Top from '../../../../component/top/index';
import Label from "../../../../component/label/index";
import PolicyManagementMenu from "../../../../component/policyManagementMenu/index";
import Title from "../../../../component/title/index";
import './index.css';
import {request} from "../../../../utils/request";

const { Option } = Select;
const { RangePicker } = DatePicker;

class CollectionList extends Component {
    constructor(props){
        super(props);
        this.state = {
            arrdown:true,
            arrProduct:false,
            // labelTheme:{
            //         title:"",
            //         item:["","","","","","","","","","",""]
            //     },
            // labelType:
            //     {
            //         title:"",
            //         item:["","","","","",""]
            //     },
            // labelProduct:{
            //     title:"",
            //     item:["","","","","","","","","","",""]
            // },
            // labelStatus:{
            //     title:"",
            //     item:["","",""]
            // },
            // labelSource:{
            //     title:"",
            //     item:["","",""]
            // }
        }
        this.columns = [
            {
                title: '',
                dataIndex: 'title',
                key: 'title',
                width:400,
                render: text => <a>{text}</a>,
            },
            {
                title: '',
                dataIndex: 'hierarchy',
                key: 'hierarchy'
            },
            {
                title: '',
                dataIndex: 'address',
                key: 'address',
            },
            {
                title: '',
                key: 'time',
                dataIndex: 'time',
                width:200
            }
        ];

        this.data = [
            {
                key: '1',
                title: '',
                hierarchy: "",
                address: '',
                theme: '',
                type:'',
                analysis:'-',
                status:'',
                source:'',
                time:'2019-02-01 12:05:11',
                money:''
            },
            {
                key: '2',
                title: '',
                hierarchy: "",
                address: '',
                theme: '',
                type:'',
                analysis:'-',
                status:'',
                source:'',
                time:'2019-02-01 12:05:11',
                money:''
            },
            {
                key: '3',
                title: '',
                hierarchy: "",
                address: '',
                theme: '',
                type:'',
                analysis:'-',
                status:'',
                source:'',
                time:'2019-02-01 12:05:11',
                money:''
            },
            {
                key: '4',
                title: '',
                hierarchy: "",
                address: '',
                theme: '',
                type:'',
                analysis:'-',
                status:'',
                source:'',
                time:'2019-02-01 12:05:11',
                money:''
            }
        ];
        function onShowSizeChange(current, pageSize) {
            console.log(current, pageSize);
        }
        this.pagination = {
            showSizeChanger:true,
            defaultCurrent:1,
            total:500,
            pageSizeOptions:['10', '20', '30', '50','100','150'],
            onShowSizeChange:onShowSizeChange
        }
    }
    async componentWillMount() {
        const labelThemeData = await request('/common/get-all-policy-theme-label', 'POST'); //
        const labelTypeData = await request('/common/get-all-use-type-label', 'POST'); //
        const selectBelongData = await request('/common/get-all-belong-label', 'POST'); //
        const selectIndustryData = await request('/common/get-all-industry-label', 'POST'); //


        const themData = labelThemeData.data;
        const typeData = labelTypeData.data;
        const belongData = selectBelongData.data;
        const industryData = selectIndustryData.data;

        if (themData && themData.success && typeData && themData.success && belongData && belongData.success && industryData && industryData.success) {
            const allItem = {id: 0,name: ""};
            themData.data.unshift(allItem);
            typeData.data.unshift(allItem);
            belongData.data.unshift(allItem);
            industryData.data.unshift(allItem);
            this.setState({
                labelTheme: {
                    title: "",
                    item: themData.data
                },
                labelType: {
                    title: "",
                    item: typeData.data
                },
                belongData: belongData.data,
                industryData: industryData.data

            })
        }
    }
    belongChange = async (value) => {
        const labelProductData = await request('/common/get-all-organization-label', 'POST', {belong_id: value}); //
        const productData = labelProductData.data;
        if (productData && productData.success) {
            this.setState({
                labelProduct: {
                    title: "",
                    item: productData.data
                }
            })
        }
    }
    setArrdown = () =>{
        this.setState({
            arrdown:!this.state.arrdown
        })
    }
    setArrProduct = () =>{
        this.setState({
            arrProduct:!this.state.arrProduct
        })
    }
    onSelectChange = selectedRowKeys => {
        console.log('selectedRowKeys changed: ', selectedRowKeys);
        this.setState({ selectedRowKeys });
    };
    render() {
        const {labelProduct,arrProduct,selectedRowKeys,belongData} = this.state;
        const rowSelection = {
            selectedRowKeys,
            onChange: this.onSelectChange,
        };
        return (
            <div className="collectionList-template">
                <Top />
                <div className="collectionList-label-box max-weight-box">
                <Row>
                    <Col span={4}>
                        <PolicyManagementMenu current="collectionList" />
                    </Col>
                    <Col span={20}>
                        <Title name="" />
                    <Breadcrumb separator=">">
                        <Breadcrumb.Item></Breadcrumb.Item>
                        <Breadcrumb.Item href=""></Breadcrumb.Item>
                    </Breadcrumb>
                    <div className="label-box">
                        <Row className="mt10">
                            <Col span={4}></Col>
                            <Col span={20}>
                                <Input />
                            </Col>
                        </Row>
                        <Row className="mt10">
                            <Col span={4}></Col>
                            <Col span={20}>
                                <Select style={{ width: 300 }} onChange={this.belongChange}>
                                    {belongData ? belongData.map((item, idx) => <Option value={item.id}
                                                                                        key={item.id}>{item.name}</Option>) : ''}
                                </Select>
                            </Col>
                        </Row>
                        <div className="label-product-box">
                            {labelProduct ? <Label title={labelProduct.title} item={labelProduct.item} key="labelProduct"
                                                   span={{title:4,label:20}} className={arrProduct ? "allLabel" : "minLabel"}/> : ''}
                            {labelProduct ? (!arrProduct ? <span onClick={this.setArrProduct}
                                                                 className="more-label"><PlusOutlined/> </span> :
                                <span onClick={this.setArrProduct}
                                      className="more-label"><MinusOutlined/> </span>) : ''} </div>
                        <Row className="mt10">
                            <Col span={4}></Col>
                            <Col span={20}>
                                <RangePicker showTime />
                            </Col>
                        </Row>
                        <div className="search-button">
                            <Button type="primary"></Button>
                            <Button className="ml15"></Button>
                        </div>
                    </div>
                        <p align="right" className="operation-button">
                            <Button type="primary">/</Button>
                            <Button type="primary" className="ml15"></Button>
                            <Button type="primary" className="ml15"></Button>
                            <Button type="primary" className="ml15"></Button>
                        </p>
                    <Table
                        rowSelection={rowSelection}
                        columns={this.columns} dataSource={this.data} pagination={this.pagination} />
                    </Col>
                </Row>
                </div>
            </div>
        );
    };
}

export default CollectionList;