/**
 * 
 * */
import React, {Component} from 'react';
import {render} from 'react-dom';
import { Breadcrumb,Descriptions,Tag,Button,Row,Col,Card,List} from 'antd';
import { StarOutlined } from '@ant-design/icons';
import axios from 'axios';
import Top from './../../component/top';
// import Footer from "../../component/footer";
import './index.css';
import {request} from "../../utils/request";
import cookie from "react-cookies";
import {message} from "antd/lib/index";


class PolicyText extends Component {
    constructor(props){
        super(props);
        this.state = {
            id:props.match.params ? props.match.params.id : null,
            policy:{}
        };
        this.listData = [
            {
                title: '',
                time:'2016-02-26',
                link:'/itemText'
            },
            {
                title: '2019',
                time:'2016-02-26',
                link:'/itemText'
            },
            {
                title: '2019',
                time:'2016-02-26',
                link:'/itemText'
            },
        ];
    }
    componentDidMount() {
        this.getDefalutData();
    }
    getDefalutData = async() =>{
        const {data} = await request(`/policy/get/${this.state.id}`, 'GET'); //
        this.setState({
            policy:data.policy,
            resource_file_list:data.resource_file_list
        })
    }
    //
    onCollection = async (id) =>{
        const responest = await request('/common/my-company-collection', 'POST',{member_id:cookie.load('userId'),resource_id:id,resource_type:1}); //
        const data = responest.data;
        if(data && data.success){
            message.success(data.msg);
            this.setState({
                isCollection:true
            });
        }else{
            message.error(data.msg);
        }
    }
    render() {
        const {policy,resource_file_list=[],isCollection} = this.state;
        const labelStr = policy.label_add_str ? policy.label_add_str.split(",") : null;
        return (
            <div className="policyText-template">
                <Top />
                <div className="policyText-label-box max-weight-box">
                    <Breadcrumb separator=">">
                        <Breadcrumb.Item></Breadcrumb.Item>
                        <Breadcrumb.Item href=""></Breadcrumb.Item>
                    </Breadcrumb>
                   <div className="policyText-descriptions">
                       <Descriptions>
                           <Descriptions.Item label="" span={3}><span>{policy.title}</span></Descriptions.Item>
                           <Descriptions.Item label=""><span title=""></span></Descriptions.Item>
                           <Descriptions.Item label="">{policy.release_date}</Descriptions.Item>
                           <Descriptions.Item label="">{policy.post_shop_name}</Descriptions.Item>
                           {labelStr ? <Descriptions.Item label="" span={3} className="labelAdd">
                               {labelStr.map((item,idx)=><Tag key={idx}>{item}</Tag>)}</Descriptions.Item> : null}
                           {labelStr ? <Descriptions.Item label="">
                               {policy.life_date}
                           </Descriptions.Item> : null}
                       </Descriptions>
                   </div>
                    <div className="policy-butn">
                        <Button onClick={()=>this.onCollection(policy.id)} type="primary" icon={<StarOutlined />}>{isCollection ? "" : "" }</Button>
                    </div>
                    <Row gutter={16} className="policyText-content-box">
                        <Col span={18} className="policyText-content">
                            {/*<p className="policyText-content-title">2018()</p>*/}
                            <div className="policyText-content-text">
                                <div dangerouslySetInnerHTML = {{ __html:policy.content }}></div>
                            </div>
                            <Row>
                                <Col span={2}></Col>
                                <Col>
                                    {resource_file_list ?
                                        resource_file_list.map((item,idx)=><p key={idx}><a href={item.image_url} key={idx}>{item.file_ori_name}</a></p>)
                                        : null}

                                </Col>
                            </Row>
                        </Col>
                        <Col span={6}>
                            <Card title="">
                                <List
                                    itemLayout="horizontal"
                                    dataSource={this.listData}
                                    renderItem={item => (
                                        <List.Item>
                                            <List.Item.Meta
                                                title={<a href={item.link} target="_blank">{item.title}</a>}
                                                description={item.time}
                                            />
                                        </List.Item>
                                    )}
                                />,
                            </Card>
                        </Col>
                    </Row>
                </div>
                {/*<Footer/>*/}
            </div>
        );
    };
}

export default PolicyText;